___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --page-bg: rgb(26, 63, 73);
  --country-delimiter-color: #5f4e57;
  --country-fill-color: #1d1a16;
  --page-text-color: #ddcfcf;
  /* This is used to set the height of the map, so adjust based on the real footer height counting the vertical paddings, min-height, etc. */
  --footer-height: 103.5px;
  --link-color: #1fa2e1;
  --menu-margin-top: 70px;
  --menu-margin-bottom: 40px;
  --distance-btw-content-and-footer: 24px;
}

@media screen and (max-width: 604px) {
  :root {
    --footer-height: 145px;
  }
  
}

html,
body {
  max-width: 100%;
  overflow-x: hidden
}

body,
h1,
h1 small {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

* {
  box-sizing: border-box;
}

.map-container,
#app,
.app-container {
  width: 100%;
}

.map-container,
#app {
  height: calc(100vh - var(--footer-height));
}

#app {
  height: 100vh;
}

.app-container {
  height: 100vh;
  position: relative;
}

.map-container img {
  width: 100%;
  max-width: 100%
}

a,
a:focus,
a:hover {
  text-decoration: none
}

.leaflet-bottom {
  bottom: unset;
  top: 0;
}

.pageLoading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: var(--page-bg);
  position: fixed;
  color: var(--page-text-color);
  font-size: 36px;
